import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/financial/payablePayment/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import NumberFormat from "../../../App/NumberFormat";

import { Modal, Tooltip, Table, Row, Col, Empty } from "antd";
import TotalMemo from "./totalMemo";
import getLanguage from "../../../App/MyLanguange";

const { getDebitMemos } = actions;

class ModalDebitMemo extends Component {
  state = {
    filter: {
      id_supplier: null,
      id_location: null,
      status: 2,
      payment_status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null,
    selectedRowKeys: [],
    selectedRows: [],
    totalDebitMemo: 0
  };

  componentDidMount() {
    const { id_location, id_supplier } = this.props;
    if (id_location && id_supplier) {
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_location: id_location
        }
      });
      this.handleLocationSupplierChange(id_location, id_supplier);
    }
  }

  componentDidUpdate(prevProps) {
    const { id_location, id_supplier, selectedData } = this.props;

    if (prevProps.selectedData !== selectedData && selectedData.detail_memos) {
      let selectedRowKeys = [];
      let selectedRows = [];
      let newTotDM = 0;
      selectedData.detail_memos.map(data => {
        selectedRowKeys.push(data.id_debit_memo);
        selectedRows.push(data);
        newTotDM += data.memo_amount
          ? parseFloat(data.memo_amount)
          : parseFloat(data.currency_amount);
      });
      // console.log(selectedRowKeys, selectedRows);
      this.setState({
        selectedRowKeys: selectedRowKeys,
        selectedRows: selectedRows,
        totalDebitMemo: newTotDM
      });
    }

    if (
      prevProps.id_location !== id_location &&
      prevProps.id_supplier !== id_supplier &&
      id_location &&
      id_supplier
    ) {
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_location: id_location
        }
      });
      this.handleLocationSupplierChange(id_location, id_supplier);
    } else if (prevProps.id_location !== id_location && id_supplier) {
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_location: id_location
        }
      });
      this.handleLocationSupplierChange(id_location, id_supplier);
    } else if (prevProps.id_supplier !== id_supplier && id_location) {
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_location: id_location
        }
      });
      this.handleLocationSupplierChange(id_location, id_supplier);
    }
  }

  handleLocationSupplierChange = (id_location, id_supplier) => {
    const { getDebitMemos } = this.props;
    getDebitMemos(id_location, id_supplier);
  };

  handleLocationChange = id_location => {
    const { getDebitMemos, id_supplier } = this.props;
    if (id_supplier) {
      getDebitMemos(id_location, id_supplier);
    }
  };

  handleSupplierChange = id_supplier => {
    const { getDebitMemos, id_location } = this.props;
    if (id_location) {
      getDebitMemos(id_location, id_supplier);
    }
  };

  handleSubmit = () => {
    const { selectedData } = this.props;
    const { selectedRowKeys, selectedRows } = this.state;
    const debitMemos = {
      detail_memos: this.state.selectedRows
    };
    const deleteKeys = selectedData.detail_memos.map(
      data => data.id_debit_memo
    );
    const data = Object.assign({}, selectedData, debitMemos);
    // console.log(selectedRowKeys, selectedRows);
    this.props.handleOkModal(data, selectedRowKeys, selectedRows, deleteKeys);
  };

  rowSelection = () => {
    return {
      onChange: (selectedRowKeys, selectedRows) => {
        let total = 0;
        selectedRows.map(data => {
          total += data.currency_amount
            ? parseFloat(data.currency_amount)
            : parseFloat(data.memo_amount);
        });
        this.setState({
          selectedRowKeys: selectedRowKeys,
          selectedRows: selectedRows,
          totalDebitMemo: total
        });
      },
      getCheckboxProps: record => ({
        disabled: record.id_payment_invoice
        // name: record.name,
      }),
      selectedRowKeys: this.state.selectedRowKeys,
      hideDefaultSelections: true
    };
  };

 

  render() {
    // console.log(this.props);

    const { visible, handleModal, payablePayment, selectedData } = this.props;
    const { totalDebitMemo } = this.state;
    const { debitMemos } = payablePayment ? payablePayment : [];
    const { loadingDebitMemos } = payablePayment ? payablePayment : false;
    const dataSource = [];
    Object.keys(debitMemos).map((item, index) => {
      if (debitMemos.length > 0) {
        debitMemos[item].memo_amount = debitMemos[item].memo_amount
          ? parseFloat(debitMemos[item].memo_amount)
          : parseFloat(debitMemos[item].currency_amount);
      }
      const find = this.props.selectedRowKeys.findIndex(id => {
        return id === debitMemos[item].id_debit_memo;
      });
      // const memoAmount = debitMemos[item].memo_amount
      //   ? parseFloat(debitMemos[item].memo_amount)
      //   : parseFloat(debitMemos[item].currency_amount);
      // const subTotal = parseFloat(selectedData.sub_total) + selectedData.sub_total_memo_amount -
      // selectedData.sub_total_discount_amount;
      if (selectedData.purchase_invoice_amount) {
        // cek dari PT atau bukan
        if (find < 0 && (
          !debitMemos[item].id_payment_invoice 
          // || debitMemos[item].id_payment_invoice === selectedData.id_purchase_invoice
        )) {
          return dataSource.push({
            ...debitMemos[item],
            key: item
          });
        }
      } else {
        return dataSource.push({
          ...debitMemos[item],
          key: item
        });
      }
    });

    let modifiedData = [];
    if (dataSource.length === 0 && Array.isArray(selectedData.detail_memos)) {
      modifiedData = selectedData.detail_memos;
    } else if (
      Array.isArray(selectedData.detail_memos) &&
      dataSource.length > 0
    ) {
      modifiedData = [...selectedData.detail_memos, ...dataSource];
    } else {
      modifiedData = dataSource;
    }

    let columns = [
      {
        title: getLanguage({id: "transactionNo"}),
        dataIndex: "debit_memo_no",
        rowKey: "debit_memo_no",
        width: "20%"
      },
      {
        title: getLanguage({id: "paymentTransaction"}),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "20%",
        render: text => {
          if (text) {
            return text;
          } else {
            return "-";
          }
        }
      },
      {
        title: getLanguage({id: "date"}),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({id: "sidebar.location"}),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "amount"}),
        dataIndex: "memo_amount",
        rowKey: "memo_amount",
        width: "20%",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    // console.log(modifiedData);

    return (
      <Modal
        title={<IntlMessages id="payablePayments.importDebitMemo" />}
        visible={visible}
        centered
        footer={
          <Row justify="space-between" type="flex">
            <Col>
              {/* <IntlMessages id="payablePayments.totalDebitMemo" /> :{" "}
              <strong style={{ fontSize: "22px" }}>
                {totalDebitMemo
                  .toFixed(2)
                  .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
              </strong> */}
              <TotalMemo
                hasError={
                  totalDebitMemo >
                  selectedData.purchase_invoice_amount -
                    selectedData.paid_amount -
                    selectedData.sub_total_discount_amount
                }
                message={
                  totalDebitMemo >
                  selectedData.purchase_invoice_amount -
                    selectedData.paid_amount -
                    selectedData.sub_total_discount_amount ? (
                    <IntlMessages id="payablePayments.totalDebitMemo.message" />
                  ) : null
                }
                value={totalDebitMemo}
              >
                <IntlMessages id="payablePayments.totalDebitMemo" />
              </TotalMemo>
            </Col>
            <Col>
              <Button
                onClick={() => {
                  // this.setState({
                  //   selectedRowKeys: [],
                  //   selectedRows: []
                  // });
                  handleModal();
                }}
              >
                <IntlMessages id="button.cancel" />
              </Button>
              <Button
                type="primary"
                onClick={() => {
                  this.handleSubmit();
                }}
                disabled={
                  totalDebitMemo >
                    selectedData.purchase_invoice_amount -
                      selectedData.paid_amount -
                      selectedData.sub_total_discount_amount ||
                  this.props.status !== 1
                }
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
        keyboard={true}
        onCancel={() => {
          // this.setState({
          //   selectedRowKeys: [],
          //   selectedRows: []
          // });
          handleModal();
        }}
        width="95%"
      >
        <Row style={{ height: "450px" }}>
          <Col>
            {modifiedData.length === 0 && !loadingDebitMemos ? (
              <Empty description={<IntlMessages id="noData" />} />
            ) : (
              <Table
                size="small"
                rowKey={record => record.id_debit_memo}
                scroll={{ y: 400 }}
                rowSelection={this.rowSelection()}
                dataSource={modifiedData}
                columns={columns}
                loading={loadingDebitMemos}
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getDebitMemos }
  )
)(ModalDebitMemo);
