const actions = {
  getPayablePayments: (page, search, filter) => ({
    type: "GET_PAYABLE_PAYMENTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPayablePayment: () => ({
    type: "CREATE_PAYABLE_PAYMENT_REQUEST"
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_FILTER_PURCHASE_INVOICES_PP_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getDebitMemos: (id_location, id_supplier) => ({
    type: "GET_FILTER_DEBIT_MEMOS_PP_REQUEST",
    payload: {
      id_location: id_location,
      id_supplier: id_supplier
    }
  }),
  storePayablePayment: data => ({
    type: "STORE_PAYABLE_PAYMENT_REQUEST",
    payload: { body: data }
  }),
  editPayablePayment: (id_payable_payment, search) => ({
    type: "EDIT_PAYABLE_PAYMENT_REQUEST",
    payload: { id_payable_payment: id_payable_payment, search: search }
  }),
  updatePayablePayment: (id_payable_payment, data) => ({
    type: "UPDATE_PAYABLE_PAYMENT_REQUEST",
    payload: {
      id_payable_payment: id_payable_payment,
      body: data
    }
  }),
  showPayablePayment: id_payable_payment => ({
    type: "SHOW_PAYABLE_PAYMENT_REQUEST",
    payload: { id_payable_payment: id_payable_payment }
  }),
  confirmPayablePayment: id_payable_payment => ({
    type: "CONFIRM_PAYABLE_PAYMENT_REQUEST",
    payload: { id_payable_payment }
  }),
  cancelPayablePayment: (id_payable_payment, data) => ({
    type: "CANCEL_PAYABLE_PAYMENT_REQUEST",
    payload: { id_payable_payment: id_payable_payment, body: data }
  }),
  uploadDocument: (id_payable_payment, data) => ({
    type: "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_REQUEST",
    payload: {
      id_payable_payment,
      body: data
    }
  }),
  downloadDocument: id_payable_payment_document => ({
    type: "DOWNLOAD_PAYABLE_PAYMENT_DOCUMENT_REQUEST",
    payload: { id_payable_payment_document }
  }),
  deleteDocument: (id_payable_payment_document, id_payable_payment) => ({
    type: "DELETE_PAYABLE_PAYMENT_DOCUMENT_REQUEST",
    payload: {
      id_payable_payment_document: id_payable_payment_document,
      id_payable_payment: id_payable_payment
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_PAYABLE_PAYMENT",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
