import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form, Input, Row, Col } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import CardWrapper from "../../../App/app.style";
import ModalDiscount from "./modalDiscount";
import ModalDebitMemo from "./modalDebitMemo";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { invoices } = props;

    this.state = {
      dataSource: [...invoices],
      count: invoices.length + 1,
      totalDPDM: 0,
      totalDiscount: 0,
      totalPaymentAmount: 0,
      visible: false,
      visibleDebitMemo: false,
      selectedRow: {},
      selectedRowKeys: [],
      selectedRows: []
    };

  }

  componentDidUpdate() {
    const {
      loading,
      loadingTransactions,
      invoice,
      invoices,
      handleSaveInvoiceList,
      clearInvoice,
      clearInvoices,
      clear
    } = this.props;

    // milih invoice sendiri
    if (!loadingTransactions && invoice) {
      const { dataSource, count, totalPaymentAmount } = this.state;
      const find = dataSource.findIndex(
        currData => currData.id_purchase_invoice === invoice.id_purchase_invoice
      );
      // console.log(find);

      // invoice baru
      if (find < 0) {
        const { debitMemos } = this.props.payablePayment;
        const { totalDPDM } = this.state;
        const findDebitMemo = debitMemos.find((x) => x.id_payment_invoice === invoice.id_purchase_invoice);

        let init = {
          index_no: count,
          id_purchase_invoice: invoice.id_purchase_invoice,
          purchase_invoice_no: invoice.purchase_invoice_no,
          purchase_invoice_amount: parseFloat(invoice.total_amount),
          invoice_rate: parseFloat(invoice.currency_rate),
          paid_amount: parseFloat(invoice.paid_amount),
          sub_total_discount_amount: 0,
          sub_total_memo_amount: 0,
          detail_memos: [],
          detail_discounts: [
            {
              id_account_discount: null,
              id_department: null,
              id_project: null,
              discount_amount: 0
            }
          ],
          outstanding: 0
        };

        if (findDebitMemo) {
          const { selectedRowKeys, selectedRows } = this.state;
          init.detail_memos.push(findDebitMemo);
          init.sub_total_memo_amount += parseFloat(findDebitMemo.memo_amount);
          this.setState({
            selectedRowKeys: [...selectedRowKeys, findDebitMemo.id_debit_memo],
            selectedRows: [...selectedRows, findDebitMemo]
          });
        }

        const payment_amount = findDebitMemo
          ? init.purchase_invoice_amount - (init.paid_amount + parseFloat(findDebitMemo.memo_amount))
          : init.purchase_invoice_amount - init.paid_amount;
        const newData = Object.assign({}, invoice, init, {
          payment_amount: payment_amount
        });

        const newTotPayAmount = totalPaymentAmount + newData.payment_amount;
        const newTotDPDM = totalDPDM + init.sub_total_memo_amount;

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalDPDM: newTotDPDM,
          totalPaymentAmount: newTotPayAmount,
        });
        clearInvoice(
          [...dataSource, newData],
          newTotPayAmount,
          newTotDPDM,
          this.state.totalDiscount
        );
        // console.log(this.props.invoice);
      }
    }
    // jika invoice dari transaksi sebelumnya
    else if (!loading && invoices.length !== 0) {
      let newData = [];
      let newTotDPDM = 0;
      let newTotDiscount = 0;
      let newTotPayAmount = 0;
      let newIndex = 0;
      let selectedRowKeys = [];
      let selectedRows = [];

      invoices.map((currData, key) => {
        let init = {};
        init.index_no = key + 1;
        init.id_purchase_invoice = currData.id_purchase_invoice;
        init.purchase_invoice_no = currData.purchase_invoice_no;
        init.purchase_invoice_amount = currData.purchase_invoice_amount
          ? parseFloat(currData.purchase_invoice_amount)
          : parseFloat(currData.total_amount);
        init.invoice_rate = currData.invoice_rate
          ? parseFloat(currData.invoice_rate)
          : parseFloat(currData.currency_rate);
        init.paid_amount = currData.paid_amount
          ? parseFloat(currData.paid_amount)
          : 0;
        init.payment_amount = currData.payment_amount
          ? parseFloat(currData.payment_amount)
          : currData.purchase_invoice_amount - currData.paid_amount;
        init.sub_total_discount_amount = currData.sub_total_discount_amount
          ? parseFloat(currData.sub_total_discount_amount)
          : 0;
        init.sub_total_memo_amount = currData.sub_total_memo_amount
          ? parseFloat(currData.sub_total_memo_amount)
          : 0;
        init.detail_discounts =
          currData.detail_discounts.length !== 0
            ? currData.detail_discounts
            : [
                {
                  id_account_discount: null,
                  id_department: null,
                  id_project: null,
                  discount_amount: 0
                }
              ];
        init.detail_memos = [...currData.detail_memos];
        init.detail_memos.map(data => {
          selectedRowKeys.push(data.id_debit_memo);
          selectedRows.push(data);
        });
        init.outstanding = 
          init.purchase_invoice_amount -
          (init.paid_amount +
          init.payment_amount +
          init.sub_total_discount_amount +
          init.sub_total_memo_amount);

        newTotDPDM += parseFloat(init.sub_total_memo_amount);
        newTotDiscount += parseFloat(init.sub_total_discount_amount);
        newTotPayAmount += parseFloat(init.payment_amount);
        newIndex = parseInt(init.index_no) + 1;
        newData.push(init);
      });

      this.setState({
        dataSource: [...newData],
        totalDPDM: newTotDPDM,
        totalDiscount: newTotDiscount,
        totalPaymentAmount: newTotPayAmount,
        count: newIndex,
        selectedRowKeys: selectedRowKeys,
        selectedRows: selectedRows
      });
      clearInvoices(newData, newTotPayAmount, newTotDPDM, newTotDiscount);
    } else if (clear) {
      this.setState({
        dataSource: [],
        count: 1,
        selectedRowKeys: [],
        selectedRows: [],
        totalDPDM: 0,
        totalDiscount: 0,
        totalPaymentAmount: 0,
      });
      handleSaveInvoiceList([], 0, 0, 0);
    }
  }

  handleDiscountModal = row => {
    const { visible } = this.state;

    visible
      ? this.setState({ visible: false, selectedRow: {} })
      : this.setState({ visible: true, selectedRow: row });
  };

  handleModalDebitMemo = row => {
    const { visibleDebitMemo } = this.state;
    visibleDebitMemo
      ? this.setState({
          visibleDebitMemo: false,
          selectedRow: {}
        })
      : this.setState({
          visibleDebitMemo: true,
          selectedRow: row ? row : {}
        });
  };

  handleOkModalDebitMemo = (row, rowKeys, rows, deleteKeys) => {
    const { visibleDebitMemo } = this.state;
    // menghilangkan selected row sebelumnya
    const selectedRowKeys = this.state.selectedRowKeys.filter(
      id => !deleteKeys.includes(id)
    );
    const selectedRows = this.state.selectedRows.filter(
      data => !deleteKeys.includes(data.id_debit_memo)
    );

    this.setState({
      visibleDebitMemo: !visibleDebitMemo,
      selectedRowKeys: Array.isArray(rowKeys)
        ? rowKeys.length > 0
          ? [...selectedRowKeys, ...rowKeys]
          : [...selectedRowKeys]
        : [],
      selectedRows: Array.isArray(rows)
        ? rows.length > 0
          ? [...selectedRows, ...rows]
          : [...selectedRows]
        : [],
      selectedRow: {}
    });
    this.handleSave(row);
  };

  handleDiscount = value => {
    this.setState({ visible: false, selectedRow: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSaveInvoiceList } = this.props;
    let newTotDPDM = 0;
    let newTotDiscount = 0;
    let newTotPayAmount = 0;
    const dataSource = this.state.dataSource;
    let newData = null;

    newData = dataSource.filter(currData => currData.index_no !== index_no);
    const deleteData = dataSource.find(
      currData => currData.index_no === index_no
    );
    const deleteKeys = deleteData.detail_memos.map(
      currData => currData.id_debit_memo
    );

    // hapus selected debit memo supaya bisa dipilih lagi
    const selectedRowKeys = this.state.selectedRowKeys.filter(
      id => !deleteKeys.includes(id)
    );
    const selectedRows = this.state.selectedRows.filter(
      data => !deleteKeys.includes(data.id_debit_memo)
    );

    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotDPDM += parseFloat(currData.sub_total_memo_amount);
      newTotDiscount += parseFloat(currData.sub_total_discount_amount);
      newTotPayAmount += parseFloat(currData.payment_amount);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalDPDM: newTotDPDM,
      totalDiscount: newTotDiscount,
      totalPaymentAmount: newTotPayAmount,
      selectedRowKeys: selectedRowKeys,
      selectedRows: selectedRows
    });
    handleSaveInvoiceList(newData, newTotPayAmount, newTotDPDM, newTotDiscount);
  };

  handleSave = row => {
    const { handleSaveInvoiceList } = this.props;
    const newData = this.state.dataSource;
    let newTotDPDM = 0;
    let newTotDiscount = 0;
    let newTotPayAmount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    let memo_amount = 0;
    row.detail_memos.map(data => {
      data.memo_amount = data.memo_amount
        ? parseFloat(data.memo_amount)
        : parseFloat(data.currency_amount);
      memo_amount += parseFloat(data.memo_amount);
    });

    let discount_amount = 0;
    row.detail_discounts.map(data => {
      discount_amount += data.discount_amount
        ? parseFloat(data.discount_amount)
        : 0;
    });

    // untuk mengubah payment amount ketika ada perubahan memo
    if (row.sub_total_memo_amount !== memo_amount && memo_amount !== 0) {
      row.payment_amount =
        row.payment_amount + row.sub_total_memo_amount - memo_amount;
    } else if (row.sub_total_memo_amount !== memo_amount && memo_amount === 0) {
      row.payment_amount += row.sub_total_memo_amount;
    }

    //untuk mengubah payment amount ketika ada perubahan diskon
    if (
      row.sub_total_discount_amount !== discount_amount &&
      discount_amount !== 0
    ) {
      row.payment_amount =
        row.payment_amount + row.sub_total_discount_amount - discount_amount;
    } else if (
      row.sub_total_discount_amount !== discount_amount &&
      discount_amount === 0
    ) {
      row.payment_amount += row.sub_total_discount_amount;
    }

    row.sub_total_discount_amount = discount_amount;
    row.sub_total_memo_amount = memo_amount;

    // sisa pembayaran yang sangat kecil
    const outstanding = 
      row.purchase_invoice_amount -
      (row.paid_amount +
      row.payment_amount +
      row.sub_total_discount_amount + 
      row.sub_total_memo_amount);

    // masukkan sisa pembayaran ke payment amount
    if (outstanding < 0.01) {
      row.payment_amount += outstanding;
      row.outstanding = 0;
    } else {
      row.outstanding = outstanding;
    }

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    newData.map(currData => {
      newTotDPDM += parseFloat(currData.sub_total_memo_amount);
      newTotDiscount += parseFloat(currData.sub_total_discount_amount);
      newTotPayAmount += parseFloat(currData.payment_amount);
    });

    this.setState({
      dataSource: [...newData],
      totalDPDM: newTotDPDM,
      totalDiscount: newTotDiscount,
      totalPaymentAmount: newTotPayAmount
    });
    handleSaveInvoiceList(newData, newTotPayAmount, newTotDPDM, newTotDiscount);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource, visible, visibleDebitMemo } = this.state;
    const { loadingTable, payablePayment } = this.props;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    let columns = [
      {
        title: getLanguage({id: "no."}),
        dataIndex: "index_no",
        align: "right",
        width: "5%",
        render: text => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 5
              }
            };
          }
          return {
            children: text,
            props: {
              colSpan: 1
            }
          };
        }
      },
      {
        title: getLanguage({id: "invoiceNo"}),
        dataIndex: "purchase_invoice_no",
        width: "20%",
        // fixed: "left",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "invoiceAmount"}),
        dataIndex: "purchase_invoice_amount",
        width: "20%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={text} />
        }
      },
      {
        title: getLanguage({id: "invoiceRate"}),
        dataIndex: "invoice_rate",
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={text} />
        }
      },
      {
        title: getLanguage({id: "paidAmount"}),
        width: "15%",
        dataIndex: "paid_amount",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={text} />
        }
      },
      {
        title: getLanguage({id: "paymentAmount"}),
        dataIndex: "payment_amount",
        width: "15%",
        align: "center",
        editable: true
      },
      // {
      //   title: getLanguage({id: "discount"}),
      //   dataIndex: "sub_total_discount_amount",
      //   width: "15%",
      //   align: "center",
      //   render: (text, row) => {
      //     if (isNaN(row.index_no)) {
      //       return {
      //         props: {
      //           colSpan: 3
      //         }
      //       };
      //     }

      //     return (
      //       <div
      //         className="editable-cell-value-wrap"
      //         onClick={() => this.handleDiscountModal(row)}
      //       >
      //         <NumberFormat value={text} />
      //       </div>
      //     );
      //   }
      // },
      {
        title: getLanguage({id: "sidebar.debitMemo"}),
        dataIndex: "sub_total_memo_amount",
        width: "25%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 2
              }
            };
          }

          return (
            <div
              className="editable-cell-value-wrap"
              onClick={() => this.handleModalDebitMemo(row)}
            >
              <NumberFormat value={text} />
            </div>
          );
        }
      },
      {
        title: getLanguage({id: "outstanding"}),
        dataIndex: "outstanding",
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={text} />
        }
      },
    ];

    let operation = 
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {   
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          else{
            return (
              <Popconfirm
                title={<IntlMessages id="confirmDelete" />}
                onConfirm={() => this.handleDelete(row.index_no)}
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Tooltip
                  title={<IntlMessages id="delete" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceDltBtn"
                    size="small"
                  >
                    <i className="ion-trash-b" />
                  </Button>
                </Tooltip>
              </Popconfirm>
            );
          }        
        }
      }

    // karena operasinya fixed jadi harus dipisah
    if ((!this.props.status || this.props.status === 1) && columns.length === 8){
      columns.push(operation)
    }
    
    let columns_final = columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: isNaN(record.index_no)
            ? false
            : this.props.status === 1
              ? true
              : false,
          dataIndex: col.dataIndex,
          title: col.title,
          status: this.props.status,
          permissions: this.props.permissions,
          handleSave: this.handleSave
        })
      };
    });
    const info = [
      {
        index_no: getLanguage({ id: "totalPaymentAmount"}),
        payment_amount: parseFloat(this.state.totalPaymentAmount)
        // .toFixed(6)
        // .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      },
      {
        index_no: getLanguage({ id: "totalMemoDP"}),
        payment_amount: parseFloat(this.state.totalDPDM)
        // .toFixed(6)
        // .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      },
      // {
      //   index_no: getLanguage({ id: "totalDiscount"}),
      //   payment_amount: parseFloat(this.state.totalDiscount)
      //   // .toFixed(6)
      //   // .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      // },
      {
        index_no: getLanguage({ id: "total"}),
        payment_amount:
          parseFloat(this.state.totalPaymentAmount) +
          parseFloat(this.state.totalDPDM) +
          parseFloat(this.state.totalDiscount)
        // .toFixed(6)
        // .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      }
    ];

    const modifiedData = dataSource.length > 0 ? [...dataSource, ...info] : [];
    return (
      <CardWrapper>
        <div>
          <ModalDiscount
            visible={visible}
            selectedData={this.state.selectedRow}
            status={this.props.status}
            handleDiscountModal={this.handleDiscountModal}
            handleDiscount={this.handleDiscount}
            submit={this.handleDiscount}
            departments={this.props.departments}
            projects={this.props.projects}
            accounts={this.props.accounts}
          />

          <ModalDebitMemo
            visible={visibleDebitMemo}
            selectedData={this.state.selectedRow}
            status={this.props.status}
            payablePayment={payablePayment}
            handleModal={this.handleModalDebitMemo}
            handleOkModal={this.handleOkModalDebitMemo}
            handleSelectModal={this.handleSelectModalDebitMemo}
            id_location={this.props.id_location}
            id_supplier={this.props.id_supplier}
            currency_code={this.props.currency_code}
            selectedRowKeys={this.state.selectedRowKeys}
            selectedRows={this.state.selectedRows}
          />

          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns_final}
              loading={loadingTable}
              pagination={false}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
