import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import PayablePaymentCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/payablePayment/actions";
import cashBankActions from "../../../../redux/cashBank/cashBank/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/financials/payable-payments";
const { storeCashBank } = cashBankActions;

class CreatePayablePayment extends Component {
  state = {
    visibleAddCashBank: false
  };

  componentDidMount() {
    const { createPayablePayment } = this.props;
    createPayablePayment();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.payablePayment.error) {
      notification("error", this.props.payablePayment.error);
      this.props.clearError();
    }
    if (
      (this.props.payablePayment.success ||
        this.props.payablePayment.warning) &&
      this.props.payablePayment.id_payable_payment
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.payablePayment.id_payable_payment
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.payablePayment.success);
      // this.props.clearSuccess();
    }

    // Cash Bank
    if (this.props.cashBank.error) {
      notification("error", this.props.cashBank.error);
      this.props.clearError();
    }

    if (this.props.cashBank.success) {
      const { createPayablePayment, clearSuccess } = this.props;
      notification("success", this.props.cashBank.success);
      this.handleModalAddCashBank();
      clearSuccess();
      createPayablePayment();
    }
  }

  handleModalAddCashBank = () => {
    this.setState({
      visibleAddCashBank: !this.state.visibleAddCashBank
    });
  };

  handleSubmit = value => {
    const { storePayablePayment } = this.props;
    storePayablePayment(value);
  };

  handleSubmitCashBank = value => {
    const { storeCashBank } = this.props;
    storeCashBank(value);
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="payablePayments.button.add" />}>
            <Button
              onClick={() => {
                removeSaveData("pp");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <PayablePaymentCreateForm
            submit={this.handleSubmit}
            payablePayment={this.props.payablePayment}
            auth={this.props.auth}
            // Cash Bank
            cashBank={this.props.cashBank}
            visibleAddCashBank={this.state.visibleAddCashBank}
            handleModalAddCashBank={this.handleModalAddCashBank}
            handleSubmitCashBank={this.handleSubmitCashBank}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    payablePayment: state.payablePayment,
    cashBank: state.cashBank
  };
};

export default compose(connect(mapStateToProps, { ...actions, storeCashBank }))(
  CreatePayablePayment
);
